import React, { Component } from 'react';
import { connect } from 'react-redux';
import { OrgUnitTreeMultipleRoots } from '@dhis2/d2-ui-org-unit-tree';
import Paper from 'material-ui/Paper';
import CircularProgress from 'material-ui/CircularProgress';
import { red500 } from 'material-ui/styles/colors';
import AsyncAutoComplete from './AsyncAutoComplete';
import RaisedButton from 'material-ui/RaisedButton';
import { Heading } from '@dhis2/d2-ui-core';
import i18n from '@dhis2/d2-i18n';
import defer from 'lodash.defer';
import PropTypes from 'prop-types';
import api from '../api';
import { orgUnitRootsSelector } from '../selectors';
const styles = {
  wrapper: {
    minHeight: '300px',
    maxHeight: '60vh',
    position: 'relative'
  },
  scrollBox: {
    position: 'relative',
    marginTop: '-12px',
    maxHeight: 'calc(60vh - 154px)',
    minHeight: 'calc(300px - 154px)',
    overflowY: 'auto',
    paddingTop: '8px',
    paddingBottom: '8px',
    borderRadius: '2px'
  },
  loader: {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)'
  },
  buttonStrip: {
    paddingTop: '16px',
    height: '36px'
  },
  buttonMargin: {
    marginRight: '8px'
  },
  header: {
    paddingBottom: 0,
    fontSize: '1.2rem',
    marginBottom: '-16px'
  },
  headerError: {
    color: red500
  },
  errorText: {
    fontSize: '0.8rem',
    marginLeft: '0.8rem'
  }
};
/**
 * Renders a d2-ui OrgUnitTreeMultipleRoots with an asyncAutoComplete above it and a button strip below
 * This component will only show buttons if you pass it a confirmSelection (func) property
 * It has been made compliant with redux form
 */

class SearchableOrgUnitTree extends Component {
  constructor(props) {
    super(props);
    this.state = {
      roots: null,
      selectedOrgUnits: [...props.selectedOrgUnits],
      orgUnitFilter: null,
      initiallyExpanded: this.getInitiallyExpandedItems(props.selectedOrgUnits)
    };
  }

  getInitiallyExpandedItems(orgUnits) {
    return orgUnits.reduce((expandedUnits, orgUnit) => {
      const strippedPath = this.removeLastPathSegment(orgUnit);

      if (strippedPath) {
        expandedUnits.push(strippedPath);
      }

      return expandedUnits;
    }, []);
  }

  getIndexOfOrgUnit(orgUnit) {
    const {
      selectedOrgUnits
    } = this.state;
    const selectedUnit = selectedOrgUnits.find(unit => unit.path === orgUnit.path);
    return selectedOrgUnits.indexOf(selectedUnit);
  }

  removeLastPathSegment({
    path
  }) {
    return path.substr(0, path.lastIndexOf('/'));
  }

  update(selectedOrgUnits, initiallyExpanded) {
    const {
      onChange,
      onBlur
    } = this.props;
    const updateObject = initiallyExpanded ? {
      selectedOrgUnits,
      initiallyExpanded
    } : {
      selectedOrgUnits
    };

    if (onChange) {
      onChange(selectedOrgUnits.map(unit => unit.id));
    }

    this.setState(updateObject); // Also call onBlur if this is available. In a redux-form the component will be 'touched' by it

    onBlur && onBlur();
  }

  toggleSelectedOrgUnits = (_, orgUnit) => {
    const {
      selectedOrgUnits
    } = this.state;
    const orgUnitIndex = this.getIndexOfOrgUnit(orgUnit);
    const newOrgUnits = orgUnitIndex === -1 ? [...selectedOrgUnits, orgUnit] : [...selectedOrgUnits.slice(0, orgUnitIndex), ...selectedOrgUnits.slice(orgUnitIndex + 1)];
    this.update(newOrgUnits, []);
  };
  selectAndShowFilteredOrgUnit = dataSourceItem => {
    const orgUnit = dataSourceItem.value;
    const {
      selectedOrgUnits
    } = this.state;
    const initiallyExpanded = [this.removeLastPathSegment(orgUnit)];
    const newOrgUnits = [...selectedOrgUnits, orgUnit];
    this.update(newOrgUnits, initiallyExpanded);
  };
  clearSelection = () => {
    this.update([]);
    defer(this.applySelection);
  };
  applySelection = () => {
    const {
      selectedOrgUnits
    } = this.state;
    const {
      confirmSelection
    } = this.props;
    confirmSelection(selectedOrgUnits);
  };

  render() {
    const {
      selectedOrgUnits,
      initiallyExpanded,
      orgUnitFilter
    } = this.state;
    const {
      roots,
      confirmSelection,
      cancel,
      orgUnitType,
      headerText,
      wrapperStyle,
      errorText
    } = this.props;
    const selected = selectedOrgUnits.map(unit => unit.path);
    const autoCompleteProps = {
      floatingLabelText: i18n.t('Search'),
      hintText: i18n.t('Enter search term'),
      fullWidth: true
    };
    const headerStyle = errorText ? { ...styles.header,
      ...styles.headerError
    } : styles.header;
    return <div style={{ ...styles.wrapper,
      ...wrapperStyle
    }}>
                {headerText ? <Heading level={4} style={headerStyle}>
                        {headerText}
                        {errorText && <span style={styles.errorText}>{errorText}</span>}
                    </Heading> : null}
                <AsyncAutoComplete autoCompleteProps={autoCompleteProps} query={api.queryOrgUnits} queryParam={orgUnitType} minCharLength={2} queryDebounceTime={375} selectHandler={this.selectAndShowFilteredOrgUnit} />
                <Paper style={styles.scrollBox}>
                    {!roots ? <CircularProgress style={styles.loader} /> : <OrgUnitTreeMultipleRoots roots={roots} onSelectClick={this.toggleSelectedOrgUnits} selected={selected} initiallyExpanded={initiallyExpanded} orgUnitsPathsToInclude={orgUnitFilter} />}
                </Paper>
                {confirmSelection ? <div style={styles.buttonStrip}>
                        <RaisedButton label={i18n.t('Apply')} primary={true} style={styles.buttonMargin} onClick={this.applySelection} disabled={!roots} />
                        <RaisedButton label={i18n.t('Clear all')} secondary={true} style={styles.buttonMargin} onClick={this.clearSelection} disabled={!roots} />
                        <RaisedButton onClick={cancel} label={i18n.t('Cancel')} />
                    </div> : null}
            </div>;
  }

}

SearchableOrgUnitTree.propTypes = {
  orgUnitType: PropTypes.string.isRequired,
  selectedOrgUnits: PropTypes.array.isRequired,
  cancel: PropTypes.func,
  confirmSelection: PropTypes.func,
  errorText: PropTypes.oneOfType([PropTypes.string, PropTypes.bool]),
  headerText: PropTypes.string,
  roots: PropTypes.array,
  wrapperStyle: PropTypes.object,
  onBlur: PropTypes.func,
  onChange: PropTypes.func
};

const mapStateToProps = (state, props) => {
  return {
    roots: orgUnitRootsSelector(props.orgUnitType, state.currentUser)
  };
};

export default connect(mapStateToProps)(SearchableOrgUnitTree);